<template>
  <a-textarea v-for="(_, idx) of values" :key="idx" v-model:value="values[idx]" :placeholder="`Text ${idx + 1}`" :rows="4" />
  <a-space direction="vertical" v-if="!error">
    <a-typography-title
        level="5"
        v-for="(row, idx) of formatedValues[0]"
        :key="idx"
        @click="setCompleted(idx)"
        @mouseenter="setActive(idx)"
        @mouseleave="setActive(null)"
        :type="activeValue === idx ? 'success' : null"
        style="cursor: pointer"
        :class="{green: this.completed.indexOf(idx) > -1}"
    >{{ activeValue === idx ? formatedValues[1][idx] : row }}</a-typography-title>
  </a-space>
  <a-typography-text type="danger" v-else>{{ error }}</a-typography-text>
</template>
<script>

const SEPARATOR = '\n'

export default {
  name: 'App',
  computed: {
    formatedValues() {
      return this.values.map(value => {
        const result = value.split(SEPARATOR)

        // return result.map(row => `<p>${row}</p>`)
        return result
      })
    },
    error() {
      return this.formatedValues[0].length != this.formatedValues[1].length ? 'Тексты невозможно отформатировать' : null
    }
  },
  data() {
    return {
      values: ['', ''],
      activeValue: null,
      completed: []
    }
  },
  methods: {
    setActive(idx) {
      this.activeValue = idx
    },
    setCompleted(idx) {
      this.completed.push(idx)
    }
  }
}
</script>

<style>
.green {
  background-color: green;
  color: white !important;
}
</style>
